import { h, Component } from 'preact';
import util from 'preact-util';
import { observer } from 'mobx-preact';
import { Text, Localizer, withText } from 'preact-i18n';

import Speed from '../../components/data/speed';
import Elevation from '../../components/data/elevation';
import Weight from '../../components/data/weight';
import Distance from '../../components/data/distance';
import Temperature from '../../components/data/temperature';

@observer
class Garmin extends Component {
    toggleDate = (e) => {
        e.stopPropagation();
        e.preventDefault();
        const { showDate } = this.state;
        this.setState({ showDate: !showDate });
    }

    ignoreGarminWorkout = async (e) => {
        e.stopPropagation();
        const id = e.target.parentElement.dataset.id || e.target.dataset.id;
        const { garminActivityDetailsStore } = this.props.stores;
		await garminActivityDetailsStore.ignoreWorkout(id);
        this.loadGarminActivities();
    }

    importGarminWorkout = async (e) => {
        e.stopPropagation();
        const id = e.target.parentElement.dataset.id || e.target.dataset.id;
        const { asTrack } = this.props;
        const { garminActivityDetailsStore, workoutStore } = this.props.stores;
		const { currentTeam } = workoutStore;
        if (asTrack) {
            await garminActivityDetailsStore.importTrack(id, currentTeam);
        } else {
            await garminActivityDetailsStore.importWorkout(id, currentTeam);
        }
        this.loadGarminActivities();
        this.loadWorkouts();
    }

    async loadGarminActivities() {
		const { garminActivityDetailsStore } = this.props.stores;
		garminActivityDetailsStore.load({ query: { hideImported: 1 }});
	}

    loadWorkouts = async () => {
		const { workoutStore } = this.props.stores;
		const { currentTeam } = workoutStore;
		await workoutStore.load({ query: { team: currentTeam } });
	}

    toggleDetail = () => {
        const { showDetail = false } = this.state;
        this.setState({
            showDetail: !showDetail,
        });
    }

    componentDidMount() {
		this.loadGarminActivities();
	}

    render() {
        const { showDate, showDetail = false } = this.state;
        const { activities = [], showDate: forceShowDate, asTrack = false } = this.props;
		const { userStore, workoutStore } = this.props.stores;
        const { currentTeam } = workoutStore;
        const { team } = userStore;

        const selectedTeam = userStore.findTeam(currentTeam || team.id);

        if (!showDetail) {
            if (activities && activities.length > 0) {
                return (<>
                    <div class='col-12 mb-3'>
                        <button type='button' class='btn btn-block btn-outline-info' onClick={this.toggleDetail}>
                            <div class={`float-right ml-2 text-center border rounded-circle imageRounded imageRoundedIcon`} style={`background-image: url('./assets/garmin-connect.png');`} />
                            <Text id='workout.index.garmin-workouts'>Garmin workouts / not imported</Text> {activities.length}
                        </button>
                    </div>
                </>);
            }
            return (<></>);
        }

        return (<>
            <div class='col-12 mb-3'>
                <div class='row'>
                    {activities && activities.length > 0 && (
                        <div
                            class='col-12 border-top border-bottom pt-3 pb-3 my-2 bg-light'
                        >
                            <button type='button' class='float-right btn btn-sm btn-link' onClick={this.toggleDetail}>
                                <i class='fas fa-caret-up' /> <Text id='garmin.hide-list'>Hide list</Text>
                            </button>
                            <h5><Text id='workout.index.garmin-workouts'>Garmin workouts / not imported</Text> {activities.length}</h5>
                            {activities && activities.map(activity => {
                                const { summary = {} } = activity;
                                const startDate = util.parseInputDate(summary.startTimeInSeconds);

                                return (<>
                                    {/* <td>{util.secToHms(activity.moving_time)}</td> */}
                                    {/* <td>{util.secToHms(activity.elapsed_time)}</td> */}

                                    {/* {
                                        "durationInSeconds": 616,
                                        "startTimeInSeconds": 1602065947,
                                        "startTimeOffsetInSeconds": 7200,
                                        "activityType": "ROAD_BIKING",
                                        "averageSpeedInMetersPerSecond": 0.152,
                                        "averagePaceInMinutesPerKilometer": 109.649124, 
                                        "activeKilocalories": 1, 
                                        "deviceName": "edge1000", 
                                        "distanceInMeters": 94.02, 
                                        "maxPaceInMinutesPerKilometer": 14.6455765, 
                                        "maxSpeedInMetersPerSecond": 1.138, 
                                        "startingLatitudeInDegree": 61.00674974732101, 
                                        "startingLongitudeInDegree": 9.975536987185478, 
                                        "totalElevationLossInMeters": 2
                                    } */}

                                    <div class='col-12 d-flex mb-4 position-relative'>
                                        <div class={`text-center border rounded-circle imageRounded`} style={`background-image: url("./assets/garmin-connect.png"); border-width: 3px !important;`}>
                                            &nbsp;
                                        </div>
                                        <div class='flex-grow-1 pl-3' style='line-height: 1.2em;'>
                                            <h5 class='my-0 mb-1' style='line-height: 1.0em;'>{summary.deviceName}</h5>
                                            {summary.activityType}<br />
                                            <small class='text-muted'>
                                                {startDate && <small onClick={this.toggleDate}>
                                                    {forceShowDate || showDate ? util.formatDate(startDate, { locale: 'no-NB' }) : util.formatDistance(startDate, new Date(), { locale: 'no-NB' })}
                                                </small>}
                                                <span class='text-muted'>
                                                    <i class='fas fa-road ml-2' /> <Distance stores={this.props.stores} value={summary.distanceInMeters / 1000} />
                                                    <i class='fas fa-mountain ml-2' /> <Elevation stores={this.props.stores} value={summary.totalElevationGainInMeters} />
                                                    <i class='fas fa-tachometer-alt ml-2' /> <Speed stores={this.props.stores} value={summary.averageSpeedInMetersPerSecond * 3.6} />
                                                    <i class='fas fa-stopwatch ml-2' /> {util.secToHms(summary.durationInSeconds)}
                                                </span>
                                            </small>
                                            <br />
                                            <button class='btn btn-sm btn-block btn-primary' onClick={this.importGarminWorkout} data-id={activity.id}>
                                                <i class='fas fa-file-import' /> {asTrack ? <Text id='garmin.import-track'>Import as track</Text> : <><Text id='garmin.import'>Import workout</Text> <i class='fas fa-arrow-right' /> {selectedTeam}</>} 
                                            </button>
                                            <button class='btn btn-sm btn-link' onClick={this.ignoreGarminWorkout} data-id={activity.id}>
                                                <i class='fas fa-trash-alt' /> <Text id='garmin.ignore'>Ignore workout</Text>
                                            </button>
                                        </div>
                                    </div>
                                </>);
                            })}

                            <button type='button' class='btn btn-block btn-primary mt-3' onClick={this.toggleDetail}>
                                <i class='fas fa-caret-up' /> <Text id='garmin.hide-list'>Hide list</Text>
                            </button>
                        </div>
                    )}

                </div>
            </div>
        </>);
    }
}

export default Garmin;
