import { h, Component } from 'preact';
import util from 'preact-util';
import { observer } from 'mobx-preact';
import { Text, Localizer, withText } from 'preact-i18n';

import Speed from '../../components/data/speed';
import Elevation from '../../components/data/elevation';
import Weight from '../../components/data/weight';
import Distance from '../../components/data/distance';
import Temperature from '../../components/data/temperature';

@observer
class Strava extends Component {
    toggleDate = (e) => {
        e.stopPropagation();
        e.preventDefault();
        const { showDate } = this.state;
        this.setState({ showDate: !showDate });
    }

    ignoreStravaWorkout = async (e) => {
        e.stopPropagation();
        const id = e.target.parentElement.dataset.id || e.target.dataset.id;
        const { stravaActivityStore } = this.props.stores;
		await stravaActivityStore.ignoreWorkout(id);
        this.loadStravaActivities();
    }

    importStravaWorkout = async (e) => {
        e.stopPropagation();
        const id = e.target.parentElement.dataset.id || e.target.dataset.id;
        const { stravaActivityStore, workoutStore } = this.props.stores;
		const { currentTeam } = workoutStore;
		await stravaActivityStore.importWorkout(id, currentTeam);
        this.loadStravaActivities();
        this.loadWorkouts();
    }

    async loadStravaActivities() {
		const { stravaActivityStore } = this.props.stores;
		stravaActivityStore.load({ query: { hideImported: 1 }});
	}

    loadWorkouts = async () => {
		const { workoutStore } = this.props.stores;
		const { currentTeam } = workoutStore;
		await workoutStore.load({ query: { team: currentTeam } });
	}

    toggleDetail = () => {
        const { showDetail = false } = this.state;
        this.setState({
            showDetail: !showDetail,
        });
    }

	componentDidMount() {
		this.loadStravaActivities();
	}

    render() {
        const { showDate, showDetail = false } = this.state;
        const { activities, showDate: forceShowDate } = this.props;
		const { userStore, workoutStore } = this.props.stores;
        const { currentTeam } = workoutStore;
        const { team } = userStore;

        const selectedTeam = userStore.findTeam(currentTeam || team.id);

         if (!showDetail) {
            if (activities && activities.length > 0) {
                return (<>
                    <div class='col-12 mb-3'>
                        <button type='button' class='btn btn-block btn-outline-info' onClick={this.toggleDetail}>
                            <div class={`float-right ml-2 text-center border rounded-circle imageRounded imageRoundedIcon`} style={`background-image: url('./assets/strava-icon.png');`} />
                            <Text id='workout.index.strava-workouts'>Strava workouts / not imported</Text> {activities.length}
                        </button>
                    </div>
                </>);
            }
            return (<></>);
        }

        return (<>
            <div class='col-12 mb-3'>
                <div class='row'>
                    {activities && activities.length > 0 && (
                        <div
                            class='col-12 border-top border-bottom pt-3 pb-3 my-2 bg-light'
                        >
                            <button type='button' class='float-right btn btn-sm btn-link' onClick={this.toggleDetail}>
                                <i class='fas fa-caret-up' /> <Text id='strava.hide-list'>Hide list</Text>
                            </button>
                            <h5><Text id='workout.index.strava-workouts'>Strava workouts / not imported</Text> {activities.length}</h5>
                            {activities && activities.map(activity => {
                                const { summary = {} } = activity;
                                const startDate = util.parseInputDate(summary.startTimeInSeconds);

                                return (<>
                                    {/* <td>{util.secToHms(activity.moving_time)}</td> */}
                                    {/* <td>{util.secToHms(activity.elapsed_time)}</td> */}

                                    <div class='col-12 d-flex mb-4 position-relative'>
                                        <div class={`text-center border rounded-circle imageRounded`} style={`background-image: url("./assets/strava-icon.png"); border-width: 3px !important;`}>
                                            &nbsp;
                                        </div>
                                        <div class='flex-grow-1 pl-3' style='line-height: 1.2em;'>
                                            <h5 class='my-0 mb-1' style='line-height: 1.0em;'>{activity.name}</h5>
                                            {activity.type}<br />
                                            <small class='text-muted'>
                                                {activity.start_date && <small onClick={this.toggleDate}>
                                                    {forceShowDate || showDate ? util.formatDate(activity.start_date, { locale: 'no-NB' }) : util.formatDistance(activity.start_date, new Date(), { locale: 'no-NB' })}
                                                </small>}
                                                <span class='text-muted'>
                                                    <i class='fas fa-road ml-2' /> <Distance stores={this.props.stores} value={activity.distance / 1000} />
                                                    <i class='fas fa-mountain ml-2' /> <Elevation stores={this.props.stores} value={activity.total_elevation_gain} />
                                                    <i class='fas fa-tachometer-alt ml-2' /> <Speed stores={this.props.stores} value={activity.average_speed * 3.6} />
                                                    <i class='fas fa-stopwatch ml-2' /> {util.secToHms(activity.elapsed_time)}
                                                </span>
                                            </small>
                                            <br />
                                            <button class='btn btn-sm btn-block btn-primary' onClick={this.importStravaWorkout} data-id={activity.id}>
                                                <i class='fas fa-file-import' /> <Text id='strava.import'>Import workout</Text> <i class='fas fa-arrow-right' /> {selectedTeam}
                                            </button>
                                            <button class='btn btn-sm btn-link' onClick={this.ignoreStravaWorkout} data-id={activity.id}>
                                                <i class='fas fa-trash-alt' /> <Text id='strava.ignore'>Ignore workout</Text>
                                            </button>
                                        </div>
                                    </div>
                                </>);
                            })}

                            <button type='button' class='btn btn-block btn-primary mt-3' onClick={this.toggleDetail}>
                                <i class='fas fa-caret-up' /> <Text id='strava.hide-list'>Hide list</Text>
                            </button>
                        </div>
                    )}
                </div>
            </div>
        </>);
    }
}

export default Strava;
